import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/Home'
import Board from 'components/Board'
import Login from 'components/Login'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home
    }, {
        path: '/homes/:id',
        name: 'Board',
        component: Board
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }]

})


